.flag-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 3px -19px;
  padding: 0;
  list-style: none;

  &--main {
    justify-content: center;
    transform: translateY(-50%);
    margin-left: -19px;

    .flag-list__item {
      position: relative;
      margin: 0 0 0 19px;

      &:hover .flag-list__text {
        @include visually-hidden-inactive;
        position: absolute;
        z-index: 1;
        left: 50%;
        bottom: -72px;
        transform: translateX(-50%);
        min-height: 57px;
        padding: 17px 21px 16px;
        font-weight: normal;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        text-transform: none;
        white-space: nowrap;
        color: #ffffff;
        background-color: #101d41;
        border-radius: 4px;

        &::before {
          content: "";
          position: absolute;
          left: calc(50% - 7px);
          top: -6px;
          width: 14px;
          height: 14px;
          background-color: #101d41;
          transform-origin: center center;
          transform: rotate(45deg);
          border-radius: 1.5px 0 0 0;
        }
      }
    }

    .flag-list__img {
      position: static;
      margin: 0;
      width: 35px;
      height: 24px;
    }
  }

  &--catalog {
    flex-direction: column;

    .flag-list__item {
      margin: 0 0 10px 0;
    }

    .flag-list__img {
      position: relative;
      left: 1px;
      top: -3px;
      width: 26px;
      height: 18px;
      margin: 0 15px 0 0;
    }
  }
}

.flag-list__item {
  display: flex;
  margin-left: 19px;
  padding: 0;
}

.flag-list__img {
  display: block;
  background-repeat: no-repeat;

  &--srilanka {
    @include bg-simple-image("flag-sri-lanka.svg");
  }

  &--thailand {
    @include bg-simple-image("flag-thailand.svg");
  }

  &--seychelles {
    @include bg-simple-image("flag-seychelles.svg");
  }

  &--belgium {
    @include bg-simple-image("flag-belgium.svg");
  }

  &--czech {
    @include bg-simple-image("flag-czech.svg");
  }

  &--united-states-of-america {
    @include bg-simple-image("flag-usa.svg");
  }

  &--australia {
    @include bg-simple-image("flag-australia.svg");
  }

  &--dominica {
    @include bg-simple-image("flag-dominica.svg");
  }

  &--united-kingdom {
    @include bg-simple-image("flag-united-kingdom.svg");
  }

  &--germany {
    @include bg-simple-image("flag-germany.svg");
    background-size: cover;
  }
}

.flag-list__text {
  position: relative;
  display: block;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
  color: #1d2e5b;
}

@media (min-width: $tablet-width) {
  .flag-list {
    &--catalog {
      flex-direction: row;

      .flag-list__item {
        margin: 0 28px 0 0;
      }

      .flag-list__img {
        left: 0;
        top: 0;
        margin: 0 10px 0 0;
        width: 35px;
        height: 24px;
      }

      .flag-list__text {
        font-size: 20px;
        line-height: 30px;
      }
    }
  }
}

@media (min-width: $desktop-width) {
  .flag-list {
    &--catalog {
      flex-direction: column;

      .flag-list__item {
        margin: 0 0 7px 0;
      }

      .flag-list__img {
        left: 0;
        top: 0;
        margin: 0 19px 0 0;
      }

      .flag-list__text {
        font-size: 20px;
        line-height: 30px;
      }
    }
  }
}
