.contact-list {
  list-style: none;
  padding-left: 0;

  &--menu,
  &--bottom-main {
    .contact-list__item {
      display: flex;
      align-items: center;
      height: 45px;
      padding-left: 10px;
    }

    .contact-list__link {
      padding-left: 51px;
      text-transform: uppercase;
      color: #192144;

      &:hover {
        color: fade_out(#192144, 1 - 0.6);
      }

      &:active {
        color: fade_out(#192144, 1 - 0.3);
      }

      &:hover .contact-list__icon {
        fill: #ffbd30;
      }

      &:active .contact-list__icon {
        fill: fade-out(#ffbd30, 1 - 0.3);
      }
    }

    .contact-list__round {
      left: -10px;
      top: -12.5px;

      @include square(45px);
      background-color: #121936;
    }

    .contact-list__icon {
      fill: #ffd74b;

      &:hover {
        fill: #ffbd30;
      }

      &:active {
        fill: fade_out(#ffbd30, 1 - 0.3);
      }
    }

    .contact-list__item--phone .contact-list__icon {
      width: 18px;
      height: 18px;
    }

    .contact-list__item--mail .contact-list__icon {
      width: 18px;
      height: 13px;
    }
  }

  &--top-main {
    .contact-list__item {
      &--phone {
        .contact-list__round {
          background-color: #ff8d30;

          &:hover {
            background-color: #f26926;
          }
        }
      }

      &--mail {
        .contact-list__round {
          background-color: #ffbd30;

          &:hover {
            background-color: #ff8d30;
          }
        }
      }
    }

    .contact-list__link {
      &:hover .contact-list__icon {
        fill: #161c35;
      }

      &:active .contact-list__icon {
        fill: fade-out(#161c35, 1 - 0.3);
      }
    }

    .contact-list__item,
    .contact-list__link,
    .contact-list__round {
      @include square(40px);
      border-radius: 50%;
    }

    .contact-list__icon {
      fill: #161c35;
    }
  }

  &--bottom-main {
    .contact-list__link {
      color: #ffd74b;

      &:hover {
        color: fade_out(#ffd74b, 1 - 0.6);
      }

      &:active {
        color: fade_out(#ffd74b, 1 - 0.3);
      }
    }

    .contact-list__item--phone .contact-list__icon {
      width: 18px;
      height: 18px;
    }

    .contact-list__item--mail .contact-list__icon {
      width: 18px;
      height: 13px;
    }
  }
}

.contact-list__link {
  position: relative;
  display: block;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;
  color: #192144;
  transition: $duration;
}

.contact-list__round {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 50%;
  transition: $duration;
}

.contact-list__icon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: no-repeat 0 0;
  fill: #010101;
  transition: $duration;

  &--contacts {
    display: block;
  }

  &--topmenu {
    display: none;
  }
}

@media (min-width: $tablet-width) {
  .contact-list {
    &--menu {
      padding-left: 57px;
    }

    &--bottom-main {
      padding-left: 0;
    }

    &--menu,
    &--bottom-main {
      .contact-list__item {
        height: 68px;
      }

      .contact-list__link {
        font-size: 28px;
        line-height: 28px;
        padding-left: 81px;
      }

      .contact-list__round {
        left: -10px;
        top: -22.5px;

        @include square(68px);
      }

      .contact-list__item--phone .contact-list__icon {
        width: 27px;
        height: 27px;
      }

      .contact-list__item--mail .contact-list__icon {
        width: 27px;
        height: 20px;
      }
    }
  }
}

@media (min-width: $desktop-width) {
  .contact-list {
    display: flex;
    padding: 0;

    &--menu {
      .contact-list__item {
        &--phone {
          .contact-list__round {
            background-color: #ff8d30;

            &:hover {
              background-color: #f26926;
            }
          }
        }

        &--mail {
          .contact-list__round {
            background-color: #ffbd30;

            &:hover {
              background-color: #ff8d30;
            }
          }
        }
      }

      .contact-list__link {
        &:hover .contact-list__icon {
          fill: #161c35;
        }

        &:active .contact-list__icon {
          fill: fade-out(#161c35, 1 - 0.3);
        }
      }

      .contact-list__item,
      .contact-list__link,
      .contact-list__round {
        @include square(50px);
        border-radius: 50%;
        padding: 0;
      }

      .contact-list__round {
        left: 0;
        top: 0;
      }

      .contact-list__icon {
        fill: #161c35;

        &--contacts {
          display: none;
        }

        &--topmenu {
          display: block;
        }
      }

      .contact-list__item--phone .contact-list__icon {
        width: 18px;
        height: 18px;
      }

      .contact-list__item--mail .contact-list__icon {
        width: 18px;
        height: 13px;
      }

      .contact-list__text {
        @include visually-hidden;
      }
    }

    &--bottom-main {
      .contact-list__item {
        padding: 0;
        margin-left: 111px;
      }

      .contact-list__link {
        padding: 0 0 0 122px;
      }

      .contact-list__round {
        left: 0;
        top: -34px;

        @include square(90px);
      }

      .contact-list__item--phone .contact-list__icon {
        width: 36px;
        height: 36px;
      }

      .contact-list__item--mail .contact-list__icon {
        width: 36px;
        height: 26px;
      }
    }
  }
}
