.step-list {
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: list;
}

.step-list__item {
  position: relative;
  width: 270px;
  min-height: 80px + 10px;
  margin-bottom: -10px;
  padding: 22px 90px calc(20px + 10px) 24px;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: #161c35;
  background-color: #ff8d30;
  border-radius: 10px 10px 0 0;

  &:nth-child(2) {
    background-color: #ffa530;
  }

  &:nth-child(3) {
    background-color: #ffbd30;
  }

  &:nth-child(4) {
    min-height: 80px;
    margin-bottom: 0;
    padding-bottom: 20px;
    background-color: #ffd74b;
    border-radius: 10px;
  }

  &::after {
    counter-increment: list;
    content: counter(list);
    position: absolute;
    right: 20px;
    top: 22px;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    color: #ffffff;
  }
}

@media (min-width: $tablet-width) {
  .step-list {
    display: flex;
    flex-wrap: wrap;
  }

  .step-list__item {
    width: 50%;
    min-height: 140px;
    margin: 0;
    padding: 31px 80px 57px 33px;
    font-size: 24px;
    line-height: 24px;

    &:nth-child(1) {
      width: calc(50% + 20px);
      border-radius: 20px 0 0 0;
    }

    &:nth-child(2) {
      margin-left: -20px;
      border-radius: 20px 20px 0 0;
    }

    &:nth-child(3) {
      width: calc(50% + 20px);
      border-radius: 0 0 0 20px;
    }

    &:nth-child(4) {
      margin-left: -20px;
      border-radius: 0 0 20px 20px;
    }

    &::after {
      right: auto;
      left: 33px;
      top: auto;
      bottom: -24px;
      font-size: 30px;
      line-height: 120px;
    }
  }
}

@media (min-width: $desktop-width) {
  .step-list__item {
    min-height: 107px;
    padding: 29px 80px 30px 32px;

    &::after {
      right: calc(33px + 20px);
      left: auto;
      top: -19px;
      bottom: auto;
      font-size: 24px;
    }

    &:nth-child(1) {
      width: calc(25% + 20px - 15px);
      margin: 0 -20px 0 0;
      border-radius: 20px 0 0 20px;
    }

    &:nth-child(2),
    &:nth-child(3) {
      width: calc(25% + 20px + 15px);
      margin: 0 -20px 0 0;
      border-radius: 20px 0 0 20px;
    }

    &:nth-child(4) {
      width: calc(25% - 15px);
      margin: 0;
      border-radius: 20px;

      &::after {
        right: 33px;
      }
    }
  }
}
